.btn-primary {
	color: $white !important;
	background-color: $primary2;
	border-color: $primary2;
	box-shadow: 0 0px 10px -5px rgba(46, 196, 159, 0.5);
	margin-right: 3px;

	&:hover {
		color: $white !important;
		background-color: #0f9f86;
		border-color: #0f9f86;
	}

	&:focus,
	&.focus {
		box-shadow: 0 0 0 2px rgba(46, 196, 159, 0.5);
		background-color: #0f9f86;
		border-color: #0f9f86;
	}

	&.disabled,
	&:disabled {
		color: $white;
		background-color: #0f9f86;
		border-color: #0f9f86;
	}

	&:not(:disabled):not(.disabled) {
		&:active,
		&.active {
			color: $white;
			background-color: #0f9f86;
			border-color: #0f9f86;
		}
	}
}

.show > .btn-primary.dropdown-toggle {
	color: $white;
	background-color: #0f9f86;
	border-color: #0f9f86;
}

.btn-primary:not(:disabled):not(.disabled) {
	&:active:focus,
	&.active:focus {
		box-shadow: 0 0 0 2px rgba(46, 196, 159, 0.1);
	}
}

.show > .btn-primary.dropdown-toggle:focus {
	box-shadow: 0 0 0 2px rgba(46, 196, 159, 0.1);
}

